import axios from 'axios';
const BASE_URL = /intellidnd.com/i.test(location.host)
    ? 'https://api.intellidnd.com/v1'
    : 'http://localhost:4000/v1';
export default async function request(target, method = 'GET', token, options = {}) {
    const { data } = await axios(Object.assign({ url: BASE_URL + target, method: method, headers: token
            ? {
                Authorization: `Bearer ${token}`
            }
            : {}, withCredentials: true }, options));
    return data;
}
